header {
  display: flex;
  width: 100%;
  margin: 25px 0;
}

.header-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  .title,
  .infos-mkt {
    display: grid;
    align-items: center;
    width: 100%;
    height: 132px;
    margin: 5px 0;
    background-color: $darkBlue;
    padding: 10px;
    border-radius: 14px;
    box-shadow: $shadow;
    backdrop-filter: blur(3px);
    border-bottom: 2px solid $color1;
    @media screen and (min-width: 767px){
      width: 49%;
    }

    ul {
      height: 100%;
    }
  }
  .title {
    position: relative;
    overflow: hidden;

    &:before {
      content: "";
      position: absolute;
      top: 2px;
      width: 100%;
      height: 52px;
      background: rgba(0, 0, 0, 0.5);
      @media screen and (min-width: 1150px) {
        top: 0;
      }
    }
  }
  .global-mkt {
    display: flex;
    p {
      margin-left: 5px;
      font-size: 1.1rem;
    }
  }
}
h1 {
  z-index: 2;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  text-align: center;
  font-size: 1.5rem;
  color: $white1;
  transform: translateY(-6px);
  background: -webkit-linear-gradient(360deg, $darkBlue, $white1);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  @media screen and (min-width: 1150px) {
    transform: translateY(-2px);
  }

  img {
    height: 17px;
    object-fit: cover;
    margin-right: 3px;
    transform: translate(3px, -4px);
  }
}
.table-filters {
  width: 100%;
  margin: 5px 0;
  .table-filters-container {
    display: flex;
    background: $darkBlue;
    border-radius: 10px;
    padding: 4px;
    justify-content: space-between;
    border-bottom: 1px solid $white1;

    .no-list-btn,
    .fav-list {
      border-radius: 10px;
      padding: 2px 10px;
      cursor: pointer;
      transition: all .3s;
      &:hover {
        filter: brightness(110%);
      }
      &.active {
        background: $white1;
        color: $darkBlue;
      }
    }
    .fav-list {
      display: flex;
      align-items: center;
      img {
        height: 14px;
        margin-left: 5px;
      }
    }
  }
}
.stable-checkbox-container {
  display: flex;
  align-items: center;
}
#stableCoin, #favList {
  display: none;
  &:checked {
    ~ label {
      background: $white1;
      color: $darkBlue;
    }
  }
  ~ label {
    transition: 0.15s;
    padding: 2px 10px;
    border-radius: 10px;
    cursor: pointer;

    &:hover {
      filter: brightness(130%);
    }
  }
}
.global-chart {
  border-radius: 14px;
  overflow: hidden;
  box-shadow: $shadow;
  text {
    font-size: 0.8rem !important;
    letter-spacing: 1px;
  }
  .custom-tooltip {
    background: rgb(59, 59, 59);
    padding: 10px;
  }
}

